#components-layout-demo-fixed .logo {
    float: left;
    width: 120px;
    height: 31px;
    margin: 16px 24px 16px 0;
    background: #cccccc;
}

#components-layout-demo-fixed .dark-logo {
    background: #0092ff;
    float: left;
    width: 120px;
    height: 31px;
    margin: 16px 24px 16px 0;
}

#components-layout-demo-fixed .ant-layout-content.site-layout {
    padding: 0 10px !important;
}

/* Selected cell */
.dark-theme .handsontable tbody th.ht__highlight, .dark-theme .handsontable thead th.ht__highlight {
    background-color: #0092ff;
}

/* Specific cell (B2) */
.dark-theme .ht_master tr td {
    background-color: #323546;
    border-right: 1px solid rgb(0, 21, 41);
    border-bottom: 1px solid rgb(0, 21, 41);
    transition: ease;
    transition-delay: initial;
}

/* Edit mode */
.dark-theme .handsontableInput {
    background-color: #323546 !important;
    color: white !important;
}

.dark-theme .handsontable th {
    background-color: rgba(0, 146, 255, .75);
    color: white;
    border-right: 1px solid rgb(0, 21, 41);
}

.dark-theme .handsontable th:first-child, .dark-theme .handsontable th:nth-child(2), .dark-theme .handsontable td:first-of-type {
    border-right: 1px solid rgb(0, 21, 41);
    border-bottom: 1px solid rgb(0, 21, 41);
    border-left: 1px solid rgb(0, 21, 41);
}

.dark-theme .handsontable tr:first-child th, .dark-theme .handsontable tr:first-child td {
    border-top: 1px solid rgb(0, 21, 41);
}

.dark-theme .handsontable th:last-child {
    border-right: 1px solid rgb(0, 21, 41);
    border-bottom: 1px solid rgb(0, 21, 41);
}

.dark-theme .handsontable.htRowHeaders thead tr th:nth-child(2) {
    border-left: 1px solid rgb(0, 21, 41);
}

.dark-theme .ant-tabs-top > .ant-tabs-nav::before, .ant-tabs-bottom > .ant-tabs-nav::before,
.dark-theme .ant-tabs-top > div > .ant-tabs-nav::before, .ant-tabs-bottom > div > .ant-tabs-nav::before {
    border-bottom: 1px solid #323546;
}

.dark-theme .ant-tabs-tab {
    color: white;
}

.ant-spin-nested-loading > div > .ant-spin .ant-spin-text {
    /*text-shadow: none !important;*/
}

.ant-carousel .slick-dots-bottom {
    bottom: -10px !important;
}

.ant-carousel .slick-dots li.slick-active {
    width: 50px !important;
}

.ant-carousel .slick-dots li button {
    height: 12px !important;
    border-radius: 10px !important;
    background: #cccccc !important;
}

.ant-carousel .slick-dots li {
    height: 10px !important;
}

.ant-carousel .slick-dots li.slick-active button {
    background: #555555 !important;
}

.ant-carousel .dark-theme .slick-dots li.slick-active button {
    background: #f1f7ff !important;
}

.ant-carousel .dark-theme .slick-dots li button {
    background: #cccccc !important;
}

.ant-tabs-tab.ant-tabs-tab-disabled {
    width: 100px;
}

.dark-theme .handsontable th, .dark-theme .handsontable td {
    background-color: #323546;
    border-right: 1px solid rgb(0, 21, 41);
    border-bottom: 1px solid rgb(0, 21, 41);
    color: #f1f7ff;
}

.light-theme .hot-multi .handsontable .htDimmed {
    color: navy;
}

.dark .pika-single {
    background: #1f1f1f;
}

.dark .pika-single .pika-label {
    background: #1f1f1f;
    color: #f1f7ff;
}

.dark .pika-single .pika-lendar .pika-button {
    background: #323546;
    color: #f1f7ff;
}

.dark .pika-single .pika-lendar .is-today .pika-button {
    color: #33aaff;
}

.dark .pika-single .pika-lendar .is-selected .pika-button {
    background: #33aaff;
    color: white;
}

.dark .pika-single .pika-lendar .pika-button:hover,
.dark .pika-single .pika-lendar .pika-row.pick-whole-week:hover .pika-button {
    background: #2980b9;
}

.pika-prev, .pika-next {
    overflow: hidden !important;
}

.pika-button {
    font-size: 12px !important;
    line-height: 15px !important;
}

.dark .ant-picker-cell-in-view.ant-picker-cell-in-range::before {
    background: #111b26 !important;
}

.dark .ant-picker-cell-in-view.ant-picker-cell-range-start:not(.ant-picker-cell-range-start-single)::before,
.dark .ant-picker-cell-in-view.ant-picker-cell-range-end:not(.ant-picker-cell-range-end-single)::before {
    background: #111b26 !important;
}

.dark .ant-picker-date-panel .ant-picker-cell-in-view.ant-picker-cell-in-range.ant-picker-cell-range-hover-start .ant-picker-cell-inner::after,
.dark .ant-picker-date-panel .ant-picker-cell-in-view.ant-picker-cell-in-range.ant-picker-cell-range-hover-end .ant-picker-cell-inner::after {
    background: rebeccapurple;
}
.ant-list-split.ant-list-something-after-last-item .ant-spin-container > .ant-list-items > .ant-list-item:last-child{
    border-bottom: none !important;
}
.light .ant-layout-header{
